import React, { useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

function MenuSidebar() {
    const navigate = useNavigate()
    return (
        <div id="menu-sidebar" data-menu-active="nav-welcome"
            class="offcanvas offcanvas-start offcanvas-detached d-block">
            <div class="menu-size" style={{ width: "230px" }}>
                <div>
                    <div class="page-title custom_title sidebar-title d-flex p-0">
                        <div class="w-100 text-center">
                            <img src={require("../images/white-logo.png")} alt="" />
                        </div>
                        <div className='contact_img d-flex'>
                            <img className='col-4' src={require("../images/white-user.png")} alt="" />
                            <p className='col-8'>Username</p>
                        </div>
                        <div className="nav_deposit d-flex align-items-center">
                            <a href="" className='col-6 depo' onClick={e => {
                            e.preventDefault()
                            navigate("/deposit")
                        }}>Deposit</a>
                            <a href="" className='col-6' onClick={e => {
                            e.preventDefault()
                            navigate("/withdraw")
                        }}>Withdrwal</a>
                        </div>
                    </div>
                    <div class="list-group list-custom list-menu-large">
                        
                        <a href="#" id="nav-homes" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/promotions")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Promotions</p> </div>
                        </a>
                        <a href="#" id="nav-comps" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/history")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Transaction History</p> </div>
                        </a>
                        <a data-bs-toggle="offcanvas" data-bs-target="#menu-highlights" href="#" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/account")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Account</p> </div>
                        </a>
                        <a data-bs-toggle="offcanvas" data-bs-target="#menu-highlights" href="#" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Login</p> </div>
                        </a>
                        <a data-bs-toggle="offcanvas" data-bs-target="#menu-highlights" href="#" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/signup")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Forgot Password</p> </div>
                        </a>
                        <a data-bs-toggle="offcanvas" data-bs-target="#menu-highlights" href="#" class="list-group-item" onClick={e => {
                            e.preventDefault()
                            navigate("/")
                        }}>
                            <div style={{padding:"2px 0px"}}> <p>Log Out</p> </div>
                        </a>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default MenuSidebar